
function showSideBar() { 
  store.dispatch({ 
    type: "showSideBar"
  })

  document.getElementById("Applet").style.filter = "blur(1px)";
  document.getElementById("PanelView").style.filter = "blur(1px)";
}

function hideSideBar() { 
  
  store.dispatch({ 
    type: "hideSideBar"
  })
  
  /*
  document.getElementById("activeLink").addEventListener("click", function() { 
	 store.dispatch({ 
	   type: "hideSideBar"
	 })
  })
  */
  
  document.getElementById("Applet").style.filter = "blur()";
  document.getElementById("PanelView").style.filter = "blur()";
}


export { 
	showSideBar, 
	hideSideBar,
}





