import React from 'react';

import { Link } from 'react-router-dom';

import { Button } from 'semantic-ui-react'

import { Sidebar, Segment, Menu, Image, Icon, Header } from 'semantic-ui-react'

import {showSideBar, hideSideBar} from './sidebar.js';



class PanelContainer extends React.Component { 

  state = { visible: false }

  componentDidMount() { 
    this.unsubscribe = store.subscribe(this.changeVal.bind(this));
  }

  componentWillUnmount() { 
    this.unsubscribe();
  }

  changeVal() { 
    this.setState({ 
      visible: store.getState().sideBar
    })
  }

  render() { 
    const { visible } = this.state
    

    return(
      <div id="PanelContainer">
            

          <Sidebar
            id="SidebarMenu"
            as={Menu}
            animation='overlay'
            width='wide'
            direction='right'
            visible={visible}
            icon='labeled'
            vertical
            inverted
          >

            <p id="sideBarDescription">{this.props.courseObj.description}</p>

            {this.props.courseObj.getCourseRoutes().map((route, i) => {
                
                if(i === this.props.pageNo) {
                  return (
                   <Menu.Item key={i} id="activeLink">
                    <Link onClick={hideSideBar} to={route.path}>
                      <p id="activeh3">{i+1}. {route.title}</p>
                    </Link>
                   </Menu.Item>
                  )        
                }
                else { 
                  return (
                   <Menu.Item key={i}>
                    <Link to={route.path}>
                      <p className="SideBarMenuItem">{i+1}. {route.title}</p>
                    </Link>
                   </Menu.Item>  
                  )
                }

              }

            )}

          </Sidebar>

          {typeof this.props.pageNo === "number" &&

           <div id="PanelView">

             <div id="header">

               <p id="headerWords"> 
                  {this.props.courseObj.courseRoutes[this.props.pageNo].title} 
               </p>


                <img id="pusherIcon" onClick={showSideBar} src="/media/pusher.png" />
                    
             </div>


              <div id="ContentView">
                

                 {this.props.children}


              </div>

              <div id="navDiv">
                <Sidebar.Pusher>

                  <div className="navElement navLeft">
                    {this.props.pageNo > 0 &&
                      <Link className="navLeft" to={this.props.courseObj.courseRoutes[this.props.pageNo -1].path}>
                        <Button className="navButton" id="backButton" >
                          <img className="buttImage buttImageBack" src="/media/back.png"/>
                          Back
                        </Button>
                      </Link>
                    }
                  </div>
                  

                  <div className="navElement counter">
                    <p>{this.props.pageNo + 1}/{this.props.courseObj.routeCount}</p>
                  </div>
                  
                  <div className="navElement">
                    {this.props.pageNo < (this.props.courseObj.routeCount -1) &&
                      <Link className="navRight" to={this.props.courseObj.courseRoutes[this.props.pageNo +1].path}>
                        <Button className="navButton" id="nextButton">
                          Next
                          <img className="buttImage buttImageForward" src="/media/forward.png"/>
                        </Button>
                      </Link> 
                    }
                  </div>
                  

                </Sidebar.Pusher>
              </div>
            

            </div>
          }


          {typeof this.props.pageNo === "undefined" &&

           <div id="PanelView">

             <div id="header">

               <p id="headerWords"> 
                {this.props.title}
               </p>


                <img id="pusherIcon" onClick={showSideBar} src="/media/pusher.png" />
                    
             </div>


              <div id="ContentView">
                

                 {this.props.children}


              </div>

              <div id="navDiv">
                <Sidebar.Pusher>

                  <div className="navElement navLeft">
                    <Link className="navLeft" to={store.getState().path}>
                      <Button className="navButton" id="backButton" >
                          <img className="buttImage buttImageBack" src="/media/back.png"/>
                        Back
                      </Button>
                    </Link>
                  </div>
                  

                  <div className="navElement counter">
                  </div>
                  
                  <div className="navElement">

                  </div>
                  

                </Sidebar.Pusher>
              </div>
            


            </div>
          }



      </div>
    )
  }
}


export default PanelContainer


