import React from 'react';
import { Link } from 'react-router-dom';
import { Button } from 'semantic-ui-react'

import VarSlider from '../../Lib/VarSlider.js'

import { InlineMath, BlockMath } from 'react-katex';

import { Sidebar, Segment, Menu, Image, Icon, Header, Popup } from 'semantic-ui-react'

import PanelContainer from '../../Lib/PanelContainer.js'

import {showSideBar, hideSideBar} from '../../Lib/sidebar.js';

import Course from '../../Lib/courseClass.js' 


let course = new Course({ 
  title: "Economic Growth: The Solow Model",
  description: "Learn this, the Solow model!", 
  path: "/course/Solow",
  return: "/learn/Solow"
})

class View0 extends React.Component { 

  render() {
    return(
      <PanelContainer courseObj={course} pageNo={0}>

        <p>
        If you have ever spoken with your grandparents about what their lives were like when they were young, most likely you learned an important lesson about economic growth: material standards of living have improved substan- tially over time for most families in most countries.This advance comes from rising incomes, which have allowed people to consume greater quantities of goods and services. 

          <Link className="infoLink" to={course.path + "/info-page"}>economists</Link>

        </p>

        <p>
        Our primary task in this chapter and the next is to develop a theory of economic growth called the Solow growth model. Our analysis in Chap- ter 3 enabled us to describe how the economy produces and uses its output at one point in time. The analysis was static—a snapshot of the economy. To explain why our national income grows, and why some economies grow faster than others, we must broaden our analysis so that it describes changes in the economy over time. By developing such a model, we make our analysis
        </p>
      </PanelContainer>
    ) 
  }

  updateApplet() { 
    g.applet0.show("full");
    g.f.show();
    g.p.show();
  }
  componentDidMount() { 
    hideSideBar();
    if(store.getState().allMounted) { 
      this.updateApplet()
    } else { 
      this.unsubscribe = store.subscribe(this.checkMounted.bind(this))
    }
    window.addEventListener("resize", this.updateApplet.bind(this))
    store.dispatch({type: "setPath", path: window.location.pathname});
  }
  checkMounted() { 
    if(store.getState().allMounted) { 
      this.updateApplet();
      this.unsubscribe();
    }
  }
}
course.addCourseRoute({ 
  path: course.path + "/",
  component: View0,
  exact: true,
  title: "Introduction"
})



class View1 extends React.Component { 

  render() {
    return(
      <PanelContainer courseObj={course} pageNo={1}>

        <p>
          The supply of goods in the Solow model is based on the production function, which states that output depends on the capital stock and the labor force: <Link className="infoLink" to={course.path + "/info-page"}> test link </Link>
        </p>

        <BlockMath>{"Y = F(K, L)"}</BlockMath>
        
        <p> 
          The Solow growth model assumes that the production function has constant returns to scale. This assumption is often considered realistic, and, as we will see shortly, it helps simplify the analysis. Recall that a production function has constant returns to scale if
        </p>

        <BlockMath>{"zY = F(zK, zL)"}</BlockMath>
        
        <p>
          for any positive number <InlineMath>{"z"}</InlineMath>. That is, if both capital and labor are multiplied by <InlineMath>{"z"}</InlineMath>, the amount of output is also multiplied by <InlineMath>{"z"}</InlineMath>.
        </p>
        
        <p>
          Production functions with constant returns to scale allow us to analyze all quan- tities in the economy relative to the size of the labor force.To see that this is true, set z = 1/L in the preceding equation to obtain
        </p>

        <BlockMath>{"Y/L = F(K/L, 1)"}</BlockMath>
        
        <p>          
          This equation shows that the amount of output per worker Y/L is a function of the amount of capital per worker K/L. (The number 1 is constant and thus can be ignored.) The assumption of constant returns to scale implies that the size of the economy—as measured by the number of workers—does not affect the relation- ship between output per worker and capital per worker.
        </p>

        <p>
          Because the size of the economy does not matter, it will prove convenient to denote all quantities in per-worker terms.We designate quantities per worker with lowercase letters, so <InlineMath>{"y = Y/L"}</InlineMath> is output per worker, and <InlineMath>{"k = K/L"}</InlineMath> is capital per worker. We can then write the production function as
        </p>

        <BlockMath>{"y = f(k)"}</BlockMath>

        <p>
        where we define <InlineMath>{"f(k) = F(k, 1)"}</InlineMath>.  
        </p>

        <p>          
          This equation shows that the amount of output per worker Y/L is a function of the amount of capital per worker K/L. (The number 1 is constant and thus can be ignored.) The assumption of constant returns to scale implies that the size of the economy—as measured by the number of workers—does not affect the relation- ship between output per worker and capital per worker.
        </p>

        <p>
          Because the size of the economy does not matter, it will prove convenient to denote all quantities in per-worker terms.We designate quantities per worker with lowercase letters, so <InlineMath>{"y = Y/L"}</InlineMath> is output per worker, and <InlineMath>{"k = K/L"}</InlineMath> is capital per worker. We can then write the production function as
        </p>

        <BlockMath>{"y = f(k)"}</BlockMath>

        <p>
        where we define <InlineMath>{"f(k) = F(k, 1)"}</InlineMath>.  
        </p>

      </PanelContainer>
    ) 
  }

  updateApplet() { 
    g.applet0.show("full");
    g.f.show();
    g.p.hide();
  }
  componentDidMount() { 
    hideSideBar();
    if(store.getState().allMounted) { 
      this.updateApplet()
    } else { 
      this.unsubscribe = store.subscribe(this.checkMounted.bind(this))
    }
    window.addEventListener("resize", this.updateApplet.bind(this))
    store.dispatch({type: "setPath", path: window.location.pathname});
  }
  checkMounted() { 
    if(store.getState().allMounted) { 
      this.updateApplet();
      this.unsubscribe();
    }
  }
}
course.addCourseRoute({ 
  path: course.path + "/production-function",
  component: View1,
  title: "The Production Function"
})


class Info extends React.Component { 

  render() {
    return(
      <PanelContainer courseObj={course} title="Thee test info box">
        hello world!
      </PanelContainer>
    ) 
  }

}
course.addInfoRoute({ 
  path: course.path + "/info-page",
  component: Info
})



export default course;



