
import React from 'react'


class VarSlider extends React.Component { 
  constructor(props) { 
    super(props)

    this.state = { 
      value: 0
    }
  }

  componentDidMount() { 
    this.unsubscribe = store.subscribe(this.changeVal.bind(this));
    document.getElementById("slider" + this.props.letter).value = this.state;
  }

  componentWillUnmount() { 
    this.unsubscribe();
  }

  changeVal() { 
    this.setState({ 
      value: g[this.props.letter].get()
    })
    document.getElementById("slider" + this.props.letter).value = g[this.props.letter].get()
  }


  slideMove(e) { 
    g[this.props.letter].set(parseFloat(e.target.value))
  }


  render() { 
   return(
     <div className="sliderDiv">
      <span>{Math.round(this.state.value * 100) / 100}</span>
      <br />
      <input id={"slider" + this.props.letter} type="range" min="0" step="0.001" max="1" onChange={this.slideMove.bind(this)} />
     </div>
   ) 
  }

}

export default VarSlider;




