import React from 'react';
import { Link } from 'react-router-dom';
import { Button } from 'semantic-ui-react'
import { List } from 'semantic-ui-react'
import { Header } from 'semantic-ui-react'


import XModelCourse from '../course/XModel/XModel.js'
import SolowCourse from '../course/Solow/Solow.js'


const learnPath = "/learn";

const learnRoutes = [];



class NavColumn extends React.Component { 

  render() { 
    return(
      <div id="linkBar">
        <ul>

          {learnRoutes.map((route, i) => 
            <li key={i}> 
              <Link to={route.path}>
                {route.title}
              </Link>
            </li>
          )}

        </ul>
      </div>
    )
  }

}

class Home extends React.Component { 

 render() { 
  return(
    <div>
      <NavColumn />

      <div id="preview">
        <Header as='h1'>Home</Header>
      </div>
    </div>
  )

 }

} 

learnRoutes.push({
    path: learnPath + "/",
    component: Home, 
    exact: true,
    title: "Home"
  })


class ModelPreview extends React.Component { 

  render() { 
    return(
      <div id="preview">
        <Header as='h1'>{this.props.course.title}</Header>
              
        <p>{this.props.course.description}</p> 

        <a href={this.props.course.routes[0].path}>Click here</a> to start.
      
        <ul>
          {this.props.course.routes.map((route, i) => 
             <li key={i}><a href={route.path}>{i+1}. {route.title}</a></li>               
          )}
        </ul>

      </div>   
    )
  }

}


class XModel extends React.Component { 

 render() { 

  return(
    <div>
      <NavColumn />

      <ModelPreview page={1} course={XModelCourse}/>

    </div>
  )
 }

}

learnRoutes.push({
  path: XModelCourse.return,
  component: XModel,
  title: XModelCourse.title
})



class Solow extends React.Component { 

 render() { 

  return(
    <div>
      <NavColumn />

      <ModelPreview page={1} course={SolowCourse}/>

    </div>
  )
 }

}

learnRoutes.push({
  path: SolowCourse.return,
  component: Solow,
  title: SolowCourse.title
})






export default learnRoutes; 




